/* App.css */

/* Reset some default styles */
body {
  margin: 0;
  padding: 0;
}

/* Main container */
.container-fluid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  transition: background-color 0.3s;
}

/* Heading */
h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

/* Weather form */
.form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
}

.form input[type="text"] {
  padding: 0.5rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  outline: none;
}

.form button {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.form button:hover {
  background-color: #0056b3;
}

/* Weather data */
.weather-data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.weather-data h2 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.weather-data p {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

/* Background colors based on weather description */
.weather-clear {
  background-color: #ffcc00;
}

.weather-clouds {
  background-color: #888888;
}

.weather-rain {
  background-color: #3377cc;
}

.weather-overcast {
  background-color: #555555;
}
