.weather-form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
  }
  
  .weather-form input {
    padding: 0.5rem;
    border: none;
    border-radius: 4px;
    margin-right: 1rem;
    font-size: 1rem;
    width: 200px;
  }
  
  .weather-form button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }
  
  .weather-form button:hover {
    background-color: #0056b3;
  }
  